$icons: (0:0);
$icons: map-merge($icons,('control': (X: 0px, Y:-78px, W: 67px, H: 63px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('fb': (X: -155px, Y:-41px, W: 31px, H: 31px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('guys': (X: -78px, Y:0px, W: 67px, H: 67px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('hands': (X: 0px, Y:0px, W: 68px, H: 68px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('insta': (X: -155px, Y:0px, W: 31px, H: 31px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('next': (X: -80px, Y:-151px, W: 17px, H: 28px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('phone': (X: -41px, Y:-151px, W: 29px, H: 29px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('prev': (X: -118px, Y:-78px, W: 17px, H: 28px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('telegram': (X: 0px, Y:-151px, W: 31px, H: 31px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('vk': (X: -155px, Y:-82px, W: 31px, H: 31px, TW: 186px, TH: 182px, IMG: '../img/sprite.png')));
