  margin-top: 10px;
}

.footer__contact a:hover {
  color: #5a55b0;
}

.btn {
  cursor: pointer;
  text-decoration: none;
  outline: none;
  border: none;
  position: relative;
}

.btn.btn-gradient {
  background-image: linear-gradient(34deg, #ff6e02 0%, #fe4b0a 47%, #fc2711 100%);
  background-image: -moz-linear-gradient(34deg, #ff6e02 0%, #fe4b0a 47%, #fc2711 100%);
  background-image: -webkit-linear-gradient(34deg, #ff6e02 0%, #fe4b0a 47%, #fc2711 100%);
